  getFilteredData(data) {
    let filtered = [...data];
    if (this.filters.status !== 'all') {
      filtered = filtered.filter(item => item.status === this.filters.status);
    }
    if (this.filters.type !== 'all') {
      filtered = filtered.filter(item => item.type === this.filters.type);
    }
    if (this.filters.search) {
      const search = this.filters.search.toLowerCase();
      filtered = filtered.filter(
        item =>
          (item.title && item.title.toLowerCase().includes(search)) ||
          (item.name && item.name.toLowerCase().includes(search))
      );
    }
    return filtered;
  }

  async loadData() {
    if (!this.useAPI) {
      this.loadFromLocalStorage();
      return;
    }

    try {
      if (!this.connectionManager.isFullyConnected()) {
        this.loadFromLocalStorage();
        return;
      }

      const [grants, donations, funders, applications, budgets, expenses, analytics] =
        await Promise.all([
          this.apiClient.get(this.API_ENDPOINTS.grants || '/api/advanced-funding/grants'),
          this.apiClient.get(this.API_ENDPOINTS.donations || '/api/advanced-funding/donations'),
          this.apiClient.get(this.API_ENDPOINTS.funders || '/api/advanced-funding/funders'),
          this.apiClient.get(
            this.API_ENDPOINTS.applications || '/api/advanced-funding/applications'
          ),
          this.apiClient.get(this.API_ENDPOINTS.budgets || '/api/advanced-funding/budgets'),
          this.apiClient.get(this.API_ENDPOINTS.expenses || '/api/advanced-funding/expenses'),
          this.apiClient.get(this.API_ENDPOINTS.analytics || '/api/advanced-funding/analytics'),
        ]);

      this.grants = grants.data || [];
      this.donations = donations.data || [];
      this.funders = funders.data || [];
      this.applications = applications.data || [];
      this.budgets = budgets.data || [];
      this.expenses = expenses.data || [];
      this.analytics = analytics.data || [];

      this.saveToLocalStorage();
      this.updateContent();
    } catch (error) {
      console.error('Error loading funding data:', error);
      this.loadFromLocalStorage();
    }
  }

  setupRealtimeSync() {
    if (!this.realtimeSync) return;
    this.realtimeSync.subscribe('advanced-funding', '*', update => {
      if (update.action === 'create' || update.action === 'update' || update.action === 'delete') {
        this.loadData();
      }
    });
  }

  setupConnectionMonitoring() {
    if (!this.connectionManager) return;
    this.connectionManager.on('online', () => {
      this.loadData();
    });
  }

